import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, PlayCircle, Printer, FileText, File, Download, UploadCloud, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput,
    Modal, ModalHeader, ModalBody, ModalFooter, Table, Progress
} from 'reactstrap'

import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import { useHistory } from 'react-router-dom'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../../CommonDataTable'
import demoCSV from './property.csv'
import { toast } from 'react-toastify'
import { Upload } from "antd"
const { Dragger } = Upload
import "antd/dist/antd.css"
import XLSX from "xlsx"
import DataTable from 'react-data-table-component'
import { IMG_BASE_URL } from '../../../Configuarables'

const Property = () => {
    const column1 = [
        {
            name: 'bath room',
            minWidth: '100px',
            // sortable: true,
            selector: 'bath_room'
        },
        {
            name: 'bed room',
            minWidth: '100px',
            // sortable: true,
            selector: 'bed_room'
        },
        {
            name: 'gas certificate',
            minWidth: '100px',
            // sortable: true,
            selector: 'gas_certificate'
        },
        {
            name: 'gas certificate expire date',
            minWidth: '100px',
            // sortable: true,
            selector: 'gas_certificate_expire_date'
        },
        {
            name: 'house details',
            minWidth: '100px',
            // sortable: true,
            selector: 'house_details'
        },
        {
            name: 'land lord id',
            minWidth: '100px',
            // sortable: true,
            selector: 'land_lord_id'
        },
        {
            name: 'latitude',
            minWidth: '100px',
            // sortable: true,
            selector: 'latitude'
        },
        {
            name: 'longitude',
            minWidth: '100px',
            // sortable: true,
            selector: 'longitude'
        },
        {
            name: 'power certificate',
            minWidth: '100px',
            // sortable: true,
            selector: 'power_certificate'
        }, {
            name: 'power certificate expire date',
            minWidth: '100px',
            // sortable: true,
            selector: 'power_certificate_expire_date'
        }, {
            name: 'property image',
            minWidth: '100px',
            // sortable: true,
            selector: 'property_image'
        }, {
            name: 'property insurence certificate',
            minWidth: '100px',
            // sortable: true,
            selector: 'property_insurence_certificate'
        },
        {
            name: 'property insurence certificate expire date',
            minWidth: '100px',
            // sortable: true,
            selector: 'property_insurence_certificate_expire_date'
        },
        {
            name: 'property rent',
            minWidth: '100px',
            // sortable: true,
            selector: 'property_rent'
        },
        {
            name: 'renter id',
            minWidth: '100px',
            // sortable: true,
            selector: 'renter_id'
        },
        {
            name: 'user id',
            minWidth: '100px',
            // sortable: true,
            selector: 'user_id'
        },
        {
            name: 'user type',
            minWidth: '100px',
            // sortable: true,
            selector: 'user_type'
        }
    ]
    const [successRows, setsuccessRows] = useState([])
    const [errorRows, seterrorRows] = useState([])
    const history = useHistory()
    const [ShowUpload, setShowUpload] = useState(true)
    const [isloading, setisloading] = useState(false)
    const [File, setFile] = useState(null)
    // const [Filename, setFilename] = useState('')
    const handleFile = (file /*:File*/) => {
        /* Boilerplate to set up FileReader */
        const reader = new FileReader()
        const rABS = !!reader.readAsBinaryString
        reader.onload = (e) => {
            /* Parse data */
            const bstr = e.target.result
            const wb = XLSX.read(bstr, { type: rABS ? "binary" : "array" })
            /* Get first worksheet */
            const wsname = wb.SheetNames[0]
            const ws = wb.Sheets[wsname]
            /* Convert array of arrays */
            const data = XLSX.utils.sheet_to_json(ws, {
                header: 1
            })
            console.log(
                "data",
                data.map((x) => { return { MSISDN: x[0] } }).filter(y => !!y).splice(1)
            )
            // setModalMSISDN(data.map((x) => { return { MSISDN: x[0] } }).filter(y => !!y).splice(1))
            /* Update state */
            //   this.setState({ data: data, cols: make_cols(ws["!ref"]) })
        }
        if (rABS) reader.readAsBinaryString(file)
        else reader.readAsArrayBuffer(file)
    }

    function beforeUpload(file) {
        // console.log(file)
        const isCSV = file.type === 'application/vnd.ms-excel'
        if (!isCSV) {
            toast.error("Only CSV file can be uploaded", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined
            })
        }
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
            toast.error("File must smaller than 2MB!", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined
            })
        }
        return isCSV && isLt2M
    }

    function getBase64(img, callback) {
        const reader = new FileReader()
        reader.addEventListener('load', () => callback(reader.result))
        reader.readAsDataURL(img)
    }

    const props = {
        name: 'file',
        multiple: false,
        action: `${IMG_BASE_URL}/api/fackupload`,
        beforeUpload,
        async onChange(info) {
            if (info.file.status === 'uploading') {
                // setloading(true)
                return
            }
            if (info.file.status === 'done') {
                // Get this url from response in real world.
                await getBase64(info.file.originFileObj, imageUrl => {
                    console.log(info.file.originFileObj, imageUrl)
                    handleFile(info.file.originFileObj)
                    setFile(imageUrl)
                })
            } else if (info.file.status === 'error') {
                toast.error(`${info.file.name} file upload failed.`)
            }
        }
    }

    const onSubmit = e => {
        e.preventDefault()
        if (!File) { toast.error('Please, upload a CSV file') } else { console.log(File) }

        setisloading(true)
        console.log({ report_file: File })
        useJwt.bulkPropertyUpload({ report_file: File }).then(res => {
            console.log(res)
            if (!res.data.payload.err) {
                setsuccessRows(res.data.payload.data)
                seterrorRows(res.data.payload.errordata)
            } else {
                toast.error('No property created')
            }
            // Success(res)
            setisloading(false)
            setFile(null)
            setShowUpload(false)
            setTimeout(() => {
                setShowUpload(true)
            }, 0)
        }).catch(err => {
            console.log(err)
            setisloading(false)
            Error(err)
        })
    }
    return (
        <>
            <Button.Ripple color='primary' className='ml-1 mb-1' onClick={e => history.goBack()}>
                <ChevronLeft size={15} />&nbsp;
                <span>Back</span>
            </Button.Ripple>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Bulk property upload</CardTitle>
                    <a href={demoCSV} download>
                        <Button.Ripple color='primary' >
                            <Download size={15} />&nbsp;
                            <span>Demo File Download</span>
                        </Button.Ripple>
                    </a>
                </CardHeader>
                <CardBody>
                    <Form className="row pt-1" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">

                        <Col sm="12" >
                            <CardBody className='px-3 py-1'>
                                {ShowUpload && <Dragger {...props}>
                                    <p className="ant-upload-drag-icon">
                                        <UploadCloud />
                                    </p>
                                    <p className="ant-upload-text">  Click / Drag file to this area to upload</p>
                                    <p className="ant-upload-hint">
                                        Support for a single or bulk property upload.
                                    </p>
                                </Dragger>}
                            </CardBody>
                            {/* </Card> */}
                        </Col>

                        <Col sm="12" className='text-center'>
                            {
                                isloading ? <Button.Ripple color='primary' className='mr-1' disabled >
                                    <Spinner color='white' size='sm' />
                                    <span className='ml-50'>Loading...</span>
                                </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit">
                                    <span >Submit</span>
                                </Button.Ripple>
                            }
                        </Col>
                    </Form>
                </CardBody>
            </Card>
            <Row className='match-height'>
                {successRows.length !== 0 && <Col md='12'>
                    <Card>
                        <CardHeader className='border-bottom '>
                            <CardTitle tag='h4'>Created  Property</CardTitle>
                        </CardHeader>
                        <CardBody>
                            <CommonDataTable column={column1} TableData={successRows} TableDataLoading={false} />
                        </CardBody>
                    </Card>
                </Col>}
                {errorRows.length !== 0 && <Col md='12'>
                    <Card>
                        <CardHeader className='border-bottom '>
                            <CardTitle tag='h4' className='text-danger'> Property create failed</CardTitle>
                        </CardHeader>
                        <CardBody>
                            <CommonDataTable column={column1} TableData={errorRows} TableDataLoading={false} />
                        </CardBody>
                    </Card>
                </Col>}
            </Row>
        </>
    )
}

export default Property